export function button({
  id,
  parameterName,
  displayValue,
}: {
  id: string;
  parameterName: string;
  displayValue: string;
}) {
  return `
<button id="${id}-${parameterName}" class="w-full rounded-lg bg-primary py-3 text-sm font-bold text-white transition-colors hover:bg-primary/90">
  ${displayValue}
</button> 
  `;
}
