export class OscillatorAudio {
  private audioContext: AudioContext;
  private oscillatorNode: OscillatorNode;

  constructor(
    audioContext: AudioContext,
    {
      frequency,
      type,
    }: {
      frequency: number;
      type: OscillatorType;
    },
  ) {
    this.audioContext = audioContext;
    this.oscillatorNode = new OscillatorNode(this.audioContext, {
      frequency,
      type,
    });
  }

  start() {
    this.oscillatorNode.start();
  }

  stop() {
    this.oscillatorNode.stop();
  }

  setFrequency(frequency: number) {
    this.oscillatorNode.frequency.setValueAtTime(
      frequency,
      this.audioContext.currentTime,
    );
  }

  setType(type: OscillatorType) {
    this.oscillatorNode.type = type;
  }

  get node() {
    return this.oscillatorNode;
  }
}
