import { UIElement } from '../../ui/UIBuilder';
import { UIEventHandler } from '../../ui/UIEventHandler';
import { OscillatorAudio } from './oscillator.audio';
import { createOscillatorUI } from './oscillator.ui';

export class OscillatorModule {
  private audioContext: AudioContext;
  private name: string;
  public elements: Array<UIElement>;
  public ui: HTMLDivElement;
  public audio: OscillatorAudio;

  constructor(
    audioContext: AudioContext,
    {
      name,
      label,
      autostart = true,
      frequency = 1000,
    }: { name: string; label: string; autostart?: boolean; frequency?: number },
  ) {
    this.name = name;
    this.audioContext = audioContext;
    const { ui, elements } = createOscillatorUI({
      name: this.name,
      label,
      frequency,
    });
    this.ui = ui;
    this.elements = elements;
    this.audio = new OscillatorAudio(this.audioContext, {
      frequency: 500,
      type: 'sine',
    });

    if (autostart) {
      this.audio.start();
    }

    this.setupEventListeners();
  }

  private setupEventListeners() {
    const eventHandlers = [
      (value?: string) => {
        this.audio.setFrequency(Number(value));
      },
      (value?: string) => {
        this.audio.setType(value as OscillatorType);
      },
    ];

    UIEventHandler.attach(this.elements, this.ui, eventHandlers);
  }
}
