import { button } from './components/button';
import { fieldset } from './components/fieldset';
import { radiobutton } from './components/radiobutton';
import { slider } from './components/slider';

export type SliderProperties = {
  type: 'slider';
  id: string;
  label: string;
  parameterName: string;
  parameters?: {
    min?: number;
    max?: number;
    value?: number;
    step?: number;
    type?: string;
  };
};

export type SliderInputProperties = Omit<SliderProperties, 'type'>;

export type ButtonProperties = {
  type: 'button';
  id: string;
  parameterName: string;
  displayValue: string;
};

export type ButtonInputProperties = Omit<ButtonProperties, 'type'>;

export type RadioButtonProperties = {
  type: 'radio';
  id: string;
  label: string;
  parameterName: string;
  parameters: Array<{
    checked?: boolean;
    value: string;
    displayValue: string;
  }>;
};

export type RadioButtonInputProperties = Omit<RadioButtonProperties, 'type'>;

export type UIElement =
  | SliderProperties
  | ButtonProperties
  | RadioButtonProperties;

export type UIBuilderResult = {
  ui: HTMLDivElement;
  elements: Array<UIElement>;
};
export class UIBuilder {
  private static elements: Array<{
    wrapper: HTMLDivElement;
    element: UIElement;
  }> = new Array();

  constructor() {}

  private static wrap(element: string) {
    const wrapper = document.createElement('div');

    wrapper.innerHTML = element;

    return wrapper;
  }

  static addSlider(properties: SliderInputProperties): typeof UIBuilder {
    const element = slider(properties);

    UIBuilder.elements.push({
      wrapper: UIBuilder.wrap(element),
      element: {
        ...properties,
        type: 'slider',
      },
    });

    return this;
  }

  static addButton(properties: ButtonInputProperties) {
    const element = button(properties);

    UIBuilder.elements.push({
      wrapper: UIBuilder.wrap(element),
      element: {
        ...properties,
        type: 'button',
      },
    });

    return this;
  }

  static addRadioButton(properties: RadioButtonInputProperties) {
    const element = radiobutton(properties);

    UIBuilder.elements.push({
      wrapper: UIBuilder.wrap(element),
      element: {
        ...properties,
        type: 'radio',
      },
    });

    return this;
  }

  static build(
    label: string,
    buildElements?: Array<HTMLDivElement>,
  ): UIBuilderResult {
    const ui = UIBuilder.wrap(fieldset({ label }));

    if (
      UIBuilder.elements.length === 0 &&
      buildElements &&
      buildElements?.length > 0
    ) {
      ui.querySelector('div')?.append(...buildElements);
      return {
        ui,
        elements: [],
      };
    }

    ui.querySelector('div')?.append(
      ...UIBuilder.elements.map((item) => item.wrapper),
    );
    const elements = UIBuilder.elements.map((item) => item.element);

    UIBuilder.elements = new Array();

    return {
      ui,
      elements,
    };
  }
}
