export class UILookup {
  static findSliderElement(
    ui: HTMLDivElement,
    { id, parameterName }: { id: string; parameterName: string },
  ) {
    const element = ui.querySelector(
      `#${id}-${parameterName}`,
    ) as HTMLInputElement;
    if (!element) {
      throw new Error(`"${id}-${parameterName}" slider not found`);
    }
    const label = ui.querySelector(
      `#${id}-${parameterName}-label`,
    ) as HTMLInputElement;
    if (!label) {
      console.log(`${id}-${parameterName}-label`); //
      throw new Error('"${id}-${parameterName}" slider label not found');
    }
    label.textContent = element.value;

    return { element, label };
  }

  static findRadioElement(
    ui: HTMLDivElement,
    { id, parameterName }: { id: string; parameterName: string },
  ) {
    const element = ui.querySelector(
      `#${id}-${parameterName}`,
    ) as HTMLInputElement;
    if (!element) {
      throw new Error('Type input not found');
    }

    return { element };
  }

  static findButtonElement(
    ui: HTMLDivElement,
    { id, parameterName }: { id: string; parameterName: string },
  ) {
    const element = ui.querySelector(
      `#${id}-${parameterName}`,
    ) as HTMLButtonElement;
    if (!element) {
      throw new Error('Trigger button not found');
    }

    return { element };
  }
}
