import { UIBuilder } from '../../ui/UIBuilder';

interface DcaUIParameters {
  name: string;
  label: string;
  gain: number;
}

export function createDcaUI(params: DcaUIParameters) {
  const ui = UIBuilder.addSlider({
    id: params.name,
    parameterName: 'dca-gain',
    label: 'Gain',
    parameters: {
      min: 0,
      max: 1000,
      value: params.gain,
      step: 1,
      type: 'range',
    },
  }).build(params.label);

  return ui;
}
