import { epsilon } from '../../utils/constant';

export class DEnvelopeAudio {
  private audioContext: AudioContext;
  public gainNode: GainNode;
  public decay: number;

  constructor(audioContext: AudioContext, { decay }: { decay: number }) {
    this.audioContext = audioContext;
    this.decay = decay;
    this.gainNode = new GainNode(this.audioContext, { gain: epsilon });
  }

  public trigger(externalCurrentTime?: number) {
    const now = externalCurrentTime || this.audioContext.currentTime;

    this.gainNode.gain.cancelScheduledValues(now);
    this.gainNode.gain.setValueAtTime(1, now);
    this.gainNode.gain.exponentialRampToValueAtTime(epsilon, now + this.decay);
  }

  setDecay(value: number) {
    this.decay = value;
  }

  get node() {
    return this.gainNode;
  }
}
