import { UIElement } from '../../ui/UIBuilder';
import { UIEventHandler } from '../../ui/UIEventHandler';
import { DEnvelopeAudio } from './d-envelope.audio';
import { createDEnvelopeUI } from './d-envelope.ui';

export class DEnvelopeModule {
  private audioContext: AudioContext;
  private name: string;
  private elements: Array<UIElement>;
  public ui: HTMLDivElement;
  public audio: DEnvelopeAudio;

  constructor(
    audioContext: AudioContext,
    { name, label }: { name: string; label: string },
  ) {
    this.name = name;
    this.audioContext = audioContext;
    const { ui, elements } = createDEnvelopeUI({
      name: this.name,
      label,
    });
    this.ui = ui;
    this.elements = elements;
    this.audio = new DEnvelopeAudio(this.audioContext, { decay: 0.8 });

    this.setupEventListeners();
  }

  private setupEventListeners() {
    const eventHandlers = [
      (value?: string) => {
        this.audio.setDecay(Number(value));
      },
      () => {
        this.audio.trigger();
      },
    ];

    UIEventHandler.attach(this.elements, this.ui, eventHandlers);
  }
}
