import { UIBuilder, UIBuilderResult } from '../../ui/UIBuilder';

interface OscillatorUIParameters {
  name: string;
  label: string;
  frequency: number;
}

export function createOscillatorUI(
  params: OscillatorUIParameters,
): UIBuilderResult {
  const ui = UIBuilder.addSlider({
    id: params.name,
    parameterName: 'frequency',
    label: 'Frequency',
    parameters: {
      min: 1,
      max: 2000,
      value: params.frequency,
      step: 1,
      type: 'range',
    },
  })
    .addRadioButton({
      id: params.name,
      label: 'Type',
      parameterName: 'type',
      parameters: [
        {
          checked: true,
          value: 'sine',
          displayValue: 'Sine',
        },
        {
          value: 'sawtooth',
          displayValue: 'Saw',
        },
        {
          value: 'square',
          displayValue: 'Square',
        },
        {
          value: 'triangle',
          displayValue: 'Triangle',
        },
      ],
    })
    .build(params.label);

  return ui;
}
