import { UIBuilder, UIBuilderResult } from '../../ui/UIBuilder';

interface DEnvelopeUIParameters {
  name: string;
  label: string;
}

export function createDEnvelopeUI(
  params: DEnvelopeUIParameters,
): UIBuilderResult {
  const ui = UIBuilder.addSlider({
    id: params.name,
    parameterName: 'd-envelope-decay',
    label: 'Decay',
    parameters: { min: 0, max: 5, value: 1, step: 0.01, type: 'range' },
  })
    .addButton({
      id: params.name,
      parameterName: 'd-envelope-trigger',
      displayValue: 'Trigger',
    })
    .build(params.label);

  return ui;
}
