import { UIElement } from '../../ui/UIBuilder';
import { UIEventHandler } from '../../ui/UIEventHandler';
import { MixerAudio } from './mixer.audio';
import { createMixerUI } from './mixer.ui';

export class MixerModule {
  private name: string;
  private elements: Array<UIElement>;
  public ui: HTMLDivElement;
  public audio: MixerAudio;

  constructor(
    audioContext: AudioContext,
    { name, label }: { name: string; label: string },
  ) {
    this.name = name;
    const { ui, elements } = createMixerUI({ name: this.name, label });
    this.ui = ui;
    this.elements = elements;
    this.audio = new MixerAudio(audioContext, {
      channel1Level: 1,
      channel2Level: 1,
      channel3Level: 1,
    });

    this.setupEventListeners();
  }

  private setupEventListeners() {
    const eventHandlers = [
      (value?: string) => {
        this.audio.setChannel1Level(Number(value));
      },
      (value?: string) => {
        this.audio.setChannel2Level(Number(value));
      },
      (value?: string) => {
        this.audio.setChannel3Level(Number(value));
      },
    ];

    UIEventHandler.attach(this.elements, this.ui, eventHandlers);
  }
}
