import { UIBuilder, UIBuilderResult } from '../../ui/UIBuilder';

interface MixerUIParameters {
  name: string;
  label: string;
}

export function createMixerUI(params: MixerUIParameters): UIBuilderResult {
  const ui = UIBuilder.addSlider({
    id: params.name,
    parameterName: 'channel-1-level',
    label: 'Channel 1',
    parameters: {
      min: 0,
      max: 1,
      step: 0.01,
      value: 0.7,
    },
  })
    .addSlider({
      id: params.name,
      parameterName: 'channel-2-level',
      label: 'Channel 2',
      parameters: {
        min: 0,
        max: 1,
        step: 0.01,
        value: 0.7,
      },
    })
    .addSlider({
      id: params.name,
      parameterName: 'channel-3-level',
      label: 'Channel 3',
      parameters: {
        min: 0,
        max: 1,
        step: 0.01,
        value: 0.7,
      },
    })
    .build(params.label);

  return ui;
}
