import { UIElement } from '../../ui/UIBuilder';
import { UIEventHandler } from '../../ui/UIEventHandler';
import { DcaAudio } from './dca.audio';
import { createDcaUI } from './dca.ui';

export class DcaModule {
  private audioContext: AudioContext;
  private name: string;
  private elements: Array<UIElement>;
  public ui: HTMLDivElement;
  public audio: DcaAudio;

  constructor(
    audioContext: AudioContext,
    {
      name,
      label,
      gain = 1000,
    }: { name: string; label: string; gain?: number },
  ) {
    this.name = name;
    this.audioContext = audioContext;
    const { ui, elements } = createDcaUI({ name: this.name, label, gain });
    this.ui = ui;
    this.elements = elements;
    this.audio = new DcaAudio(this.audioContext, { gain: 1 });

    this.setupEventListeners();
  }

  private setupEventListeners() {
    const eventHandlers = [
      (value?: string) => {
        this.audio.setGain(Number(value));
      },
    ];

    UIEventHandler.attach(this.elements, this.ui, eventHandlers);
  }
}
